import React from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import errorImg from "./images/404.png"

const NotFoundPage = () => (
  <Layout>
    <SEO title="404: Not found" />
    <div class="col-md-12">
      <br />
      <br />
      <div class="d-flex justify-content-center">
        <div class="col-md-10">
          <div class="alert alert-danger" role="alert">
            <h1>
              غير موجــود <i class="fas fa-exclamation-triangle"></i>
            </h1>
          </div>
          <br />
          <b>تعذر فتح هذة الصفحة يمكنك العودة للصفحة الرئيسية</b>
          <br />
          <br />
          <div class="col-md-8">
            <img src={errorImg} class="center" height="220" alt="error"></img>
          </div>
          <br />
          <br />
          <br />
          <br />
          <br />
          <Link to="/" class="btn btn-danger btn-sm">
            <i class="far fa-arrow-alt-circle-left"></i> العودة للصفحة الرئيسية
          </Link>

          <br />
          <br />
          <br />
          <br />
          <br />
        </div>
      </div>
    </div>
  </Layout>
)

export default NotFoundPage
